.container > div > div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text {
  pointer-events: none;
  justify-content: start !important;
  font-family: 'Kanit', sans-serif;
  line-height: 0px;
  text-transform: uppercase;
}

.number {
  font-size: 450px;
  color: #373c4c;
}

.number span {
  display: inline-block;
  position: relative;
  transform: translate3d(-35%, 0, 0);
}

.header {
  margin-left: 350px;
  font-size: 70px;
  color: white;
}

.stripe {
  height: 2px;
  width: auto;
}

.slopeBegin {
  background-color: #20232f;
  clip-path: polygon(20vw 0, 70% 0, calc(70% - 20vw) 100%, 0 100%);
}

.slopeEnd {
  clip-path: polygon(70% 0, 100% 0, calc(100% - 20vw) 100%, calc(70% - 20vw) 100%);
}

.slopeBegin,
.slopeEnd {
  position: absolute;
  width: 140%;
  height: 100%;
  cursor: pointer;
}

.pink {
  background: linear-gradient(to right, deeppink 0%, coral 100%);
}

.teal {
  background: linear-gradient(to right, SlateBlue 0%, DeepSkyBlue 100%);
}

.tomato {
  background: linear-gradient(to right, tomato 0%, gold 100%);
}

.image{
  position: relative;
  top: 100px;
  width: 60%;
}

.AlexTitle{
  font-family: Reem Kufi,sans-serif;
  font-size: 7rem;
  font-weight: 700;
  color:#e3e3e3;
}

.SectionText{
  font-family: Roboto,sans-serif;
  margin: 1.5rem 0;
  color:#e3e3e3;
}


//Understand this alex

@function random-color() {
  $value: "black";
  @if random(5) == 1 {
   $value: "#DAF7A6";
  }
  @else if random(5) == 2{
    $value: "#FFC300";
  }
  @else if random(5) == 3{
    $value: "#FF5733";
  }
  @else if random(5) == 4{
    $value: "#82E0AA";
  }
  @else {
    $value: "#85C1E9";
  }
  //returns the string value;
  @return unquote($value);
}

// n is number of stars required
@function multiple-box-shadow($n) {
  $value: "#{random(2000)}px #{random(2000)}px #{random-color()}";

  @for $i from 2 through $n {
    $value: "#{$value} , #{random(2000)}px #{random(2000)}px #{random-color()}";
  }

  @return unquote($value);
}


$shadows-small: multiple-box-shadow(700);
$shadows-medium: multiple-box-shadow(200);
$shadows-big: multiple-box-shadow(100);

.parallax {
  #stars1 {
    width: 4px;
    height: 4px;
    border-radius: 60%;
    background: transparent;
    box-shadow: $shadows-small;
    animation: animStar 150s linear infinite;
    &:after {
      content: " ";
      position: absolute;
      top: 2000px;
      width: 4px;
      height: 4px;
      border-radius: 60%;
      background: transparent;
      box-shadow: $shadows-small;
    }
  }
  
  #stars2 {
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: transparent;
    box-shadow: $shadows-medium;
    animation: animStar 100s linear infinite;
  
    &:after {
      content: " ";
      position: absolute;
      top: 2000px;
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background: transparent;
      box-shadow: $shadows-medium;
    }
  }
  
.pulsate{
  animation: pulsate 5s linear infinite;

}

  #stars3 {
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: transparent;
    box-shadow: $shadows-big;
    animation: animStar 50s linear infinite;
    &:after {
      content: " ";
      position: absolute;
      top: 2000px;
      width: 7px;
      height: 7px;
      border-radius: 50%;
      background: transparent;
      box-shadow: $shadows-big;
    }
  }
  
  @keyframes animStar {
    from {
      transform: translateY(-2000px);
      
    }
    to {
      transform: translateY(0px);

    }
  }
  @keyframes pulsate  {
    from { opacity: 1.0; }
    to { opacity: 0; }
}
}
